import { useTranslation } from "react-i18next";
import { useTheme } from "@emotion/react";
import { v4 as uuidv4 } from "uuid";
import Dhekr from "./dhekr";
import "../App.css";

// MUI Components
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import ToggleButton from "@mui/material/ToggleButton";

// React
import { useState, useEffect, useMemo } from "react";

export default function Adhkar() {
  const theme = useTheme();
  const { t } = useTranslation();
  const morningDhekr = useMemo(
    () => [
      {
        id: uuidv4(),
        count: 1,
        text: "                                                   ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                     ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "          ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "        ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "          ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "         ,                          ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                ,                  ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                                                      ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                                       ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "               ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "               ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "                                 ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "        (1)   (2)     (3)      (4)",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "        (1)     (2)      (3)      (4)      (5)",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "        (1)   (2)   (3)     (4)      (5)    (6)",
      },
      {
        id: uuidv4(),
        count: 7,
        text: "           ",
      },
      {
        id: uuidv4(),
        count: 4,
        text: "                    ",
      },
      {
        id: uuidv4(),
        count: 10,
        text: "                  ",
      },
      {
        id: uuidv4(),
        count: 100,
        text: "  ",
      },
      {
        id: uuidv4(),
        count: 100,
        text: " ",
      },
      {
        id: uuidv4(),
        count: 100,
        text: "     ,                ",
      },
    ],
    []
  );

  const eveningDhekr = useMemo(
    () => [
      {
        id: uuidv4(),
        count: 1,
        text: "                                                   ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                     ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "          ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "        ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "  ,        ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "         ,                          ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                ,                  ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                             ,         ,                ",
      },
      {
        id: uuidv4(),
        count: 1,
        text: "                                       ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "               ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "               ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "                                 ",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "        (1)   (2)     (3)      (4)",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "        (1)     (2)      (3)      (4)      (5)",
      },
      {
        id: uuidv4(),
        count: 3,
        text: "        (1)   (2)   (3)     (4)      (5)    (6)",
      },
      {
        id: uuidv4(),
        count: 7,
        text: "           ",
      },
      {
        id: uuidv4(),
        count: 4,
        text: "  ,                  ",
      },
      {
        id: uuidv4(),
        count: 10,
        text: "                  ",
      },
      {
        id: uuidv4(),
        count: 100,
        text: "  ",
      },
      {
        id: uuidv4(),
        count: 100,
        text: " ",
      },
      {
        id: uuidv4(),
        count: 100,
        text: "     ,                ",
      },
    ],
    []
  );

  let [dhekr, setDhekr] = useState(morningDhekr);
  const [alignment, setAlignment] = useState(" ");

  useEffect(() => {
    if (alignment === " ") {
      setDhekr(morningDhekr);
    } else if (alignment === " ") {
      setDhekr(eveningDhekr);
    }
  }, [alignment, eveningDhekr, morningDhekr]);
  const adhkar = dhekr.map((element) => {
    return <Dhekr key={element.id} element={element} />;
  });
  return (
    <div
      style={{
        background: theme.palette.background.paper,
        paddingBlock: "30px",
      }}
    >
      <div className="title" style={{ color: "theme.primary" }}>
        <h2 style={{ color: theme.primary }}>{t("Adhkar")}</h2>
      </div>
      <h4 style={{ fontWeight: "600", marginBottom: "10px" }}>
        {t("Fortress of the Muslim")}
      </h4>
      <ToggleButtonGroup
        color="secondary"
        value={alignment}
        exclusive
        aria-label="Platform"
        dir="ltr"
      >
        <ToggleButton
          onClick={() => {
            setAlignment(" ");
          }}
          value=" "
          sx={{ color: "primary.main" }}
        >
          {t("Morning Adhkar")}
        </ToggleButton>
        <ToggleButton
          onClick={() => {
            setAlignment(" ");
          }}
          value=" "
          sx={{ color: "primary.main" }}
        >
          {t("Evening Adhkar")}
        </ToggleButton>
      </ToggleButtonGroup>
      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          justifyContent: "space-around",
          gap: "20px",
          padding: "20px",
        }}
      >
        {adhkar}
      </div>
    </div>
  );
}
